import {
  ActionConfig,
  BaseConnectorConfig,
  isActionConfigType,
  isBaseConnectorConfigType
} from '@xip-online-data/types';
import {isSambaConfig, SambaConfig} from "@xip-online-data/samba-client";

export interface SambaSinkConfig {
  action: ActionConfig;
  samba: SambaConfig;
}

export function isSambaSinkConfigType(obj: SambaSinkConfig): obj is SambaSinkConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    isActionConfigType(obj.action) &&
    obj.samba !== undefined &&
    isSambaConfig(obj.samba)
  );
}
